<template>
    <div>
        <Tip :title="type" class="f-border-b"></Tip>
        <Form :model="formItem" label-position="right" :label-width="100" ref="formValidate" :rules="ruleValidate"
              style="width:50%;margin-left:20%;">
            <FormItem label="分类" prop="itemSort">
                <Cascader
                        v-model="formItem.typeId"
                        :data="this.itemSortListRaw"
                        trigger="hover"
                        @on-change="setVal"
                        filterable>
                </Cascader>
            </FormItem>

            <FormItem label="品牌" prop="brand">
                <Input v-model="formItem.brand" placeholder="品牌"></Input>
            </FormItem>
            <FormItem label="商品名称" prop="itemName">
                <Input v-model="formItem.itemName" placeholder="商品名称"></Input>
            </FormItem>
            <FormItem label="商品英文名称" prop="itemEnName">
                <Input v-model="formItem.itemEnName" placeholder="商品英文名称"></Input>
            </FormItem>

            <FormItem label="规格" prop="spec">
                <Input v-model="formItem.spec" placeholder="规格"/>
            </FormItem>
            <FormItem label="净重" prop="nw">
                <Input v-model.number="formItem.nw"  type="number" placeholder="净重">
                    <span slot="append">g</span>
                </Input>
            </FormItem>

            <FormItem label="销售价格" prop="sellingPrice">
                <Input v-model.number="formItem.sellingPrice" type="number" placeholder="销售价格">
                    <Icon type="logo-usd" slot="prefix" />
                </Input>
                <div style="height: 10px;text-align: left; width: 50px;color:orangered">￥{{formItem.sellingPrice*5}}</div>
            </FormItem>
            <FormItem label="进货价格" prop="costPrice">
                <Input v-model.number="formItem.costPrice" type="number" placeholder="进货价格">
                    <Icon type="logo-usd" slot="prefix" />
                </Input>
                <div style="height: 10px;text-align: left; width: 50px;color:orangered">￥{{formItem.costPrice*5}} </div>
            </FormItem>
            <FormItem label="库存" prop="inventory">
                <Input v-model="formItem.inventory" placeholder="库存"/>
            </FormItem>
            <FormItem label="描述" prop="description">
                <Input v-model="formItem.description" type="textarea" placeholder="描述"/>
            </FormItem>
            <FormItem>
                <UploadPic ref="upload" v-if="show" :str="formItem.str" :fileIds="formItem.fileIds"></UploadPic>
            </FormItem>

            <FormItem>
                <Button type="primary" @click="submit('formValidate')">提交</Button>
                <Button type="primary" @click="goBack" style="margin-left: 8px">返回</Button>
            </FormItem>
            <Button type="error" @click="gets" v-if="false"> 1111111</Button>
        </Form>
    </div>
</template>

<script>
    import _apiitems from '@/api/items'
    import Tip from '@/components/Tip'
    import UploadPic from '@/components/upload-pic'

    export default {
        name: "items-view",
        props: {
            type: {
                type: String
            },
            title: {
                type: String
            },
            itemId: {
                type: String,
                default: ''
            },
        },
        components: {
            Tip,
            UploadPic,
        },
        data() {
            return {
                show: false,
                itemSortListRaw: [],
                formItem: {
                    typeId: [],
                    itemName: '',
                    itemEnName: '',
                    brand: '',
                    spec: '',
                    nw: '',
                    sellingPrice: 0,
                    costPrice: 0,
                    inventory: '',
                    description: '',
                    fileIds: [],
                    str: '66'
                },
                ruleValidate: {
                    /* itemSort: [
                         {required: true, message: "请选择分类", trigger: 'blur'}
                     ],*/
                    itemName: [
                        {required: true, message: "请输入商品名称", trigger: 'blur'}
                    ],
                    brand: [
                        {required: true, message: "请输入品牌", trigger: 'blur'}
                    ],
                    spec: [
                        {required: true, message: "请输入规格", trigger: 'blur'}
                    ],
                    costPrice: [
                        {type: 'number', required: true, message: "请输入进价", trigger: 'blur'}
                    ],
                },
            }
        },
        computed: {},
        mounted() {
            this.itemSortList();
        },
        created() {
            if (this.title == 'add') {
                this.show = true
            } else if (this.title == 'update') {
                _apiitems.selectByPk(this.itemId).then(res => {
                    this.formItem = res.data;
                    this.formItem.str = "2526";
                    this.show = true

                })
            }
        },
        methods: {
            gets() {
                // this.$refs.upload.uploadList.forEach(this.makeList)
                // console.log(this.formItem.fileIds)
                // console.log(this.itemSortListRaw)
            },
            setVal(val) {
                console.log(val)
            },
            makeList(data) {
                //处理上传图片的id
                return this.formItem.fileIds.push(data.picId)
            },
            cascaderData(data) {
                //处理商品分类的级联选择数据
                let tem = {};
                tem.label = data.label;
                tem.value = data.value;
                tem.children = data.children
                return this.itemSortListRaw.push(tem)
            },
            itemSortList() {
                _apiitems.getItemSortAll().then(res => {
                    res.data.forEach(this.cascaderData);
                }).catch(err => {
                    console.log(err)
                    this.$Notice.error({title: '查询分类失败!'})
                });
            },
            submit(name) {
                console.log(typeof this.formItem.costPrice);
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        if (this.title == 'add') {
                            this.$refs.upload.uploadList.forEach(this.makeList)
                            _apiitems.addItem({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '新增成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.formItem.itemPicIds = []
                                this.$Notice.error({title: '新增失败!'})
                            })
                        } else {
                            this.formItem.fileIds = []
                            this.$refs.upload.uploadList.forEach(this.makeList)
                            _apiitems.updateItem({...this.formItem}).then(res => {
                                if (res) {
                                    this.$Notice.success({title: '修改成功!'})
                                    this.$emit('childFn', 'list');
                                }
                            }).catch(err => {
                                this.formItem.itemPicIds = []
                                this.$Notice.error({title: '修改失败!'})
                            })
                        }
                    } else {
                        this.$Notice.error({title: '有数据未填写!'})
                    }
                })
            },
            goBack() {
                this.$emit('childFn', 'list');
            },
        }
    }
</script>

